body{
    background-color: white;
}
.Login{
   padding: 25px;
   padding-top: 15px;
   height: 100vh;
   display: flex;
   flex-direction: column;
   gap: 70px;
}
.login__header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
}
.login__language{
    display: flex;
    gap: 5px;
    align-items: flex-end;
    cursor: pointer;
    padding: 5px 10px;
    border-radius: 50px;
    font-size: 15px;
    transition: all 0.2s;
}
.login__language:hover{
    background-color: hsla(0,0%, 50.2%, 0.125);
}
.login__language span{
    font-weight: 500;
}

.login__info{
    display: flex;
    flex-direction: column;
    gap: 20px;
    min-width: 330px;
    margin-left:auto ;
    margin-right: auto;
}
.login__info h1{
    font-weight: 500;
    font-size: 40px;
    margin-left: -201px;
}
.login__form{
    display: flex;
    flex-direction: column;
}
.login__form label{
    color: #5c5e62;
    font-size: 15px;
    font-weight: 500;
    margin-left: -220px;
}
.login__form input{
    margin-bottom: 30px;
    background-color: #f4f4f4;
    border: 1px solid  #f4f4f4 ;
    border-radius: 50px;
    padding: 12px 20px;
    color: #393c41;
    font-weight: 600;
    border: 1px solid rgb(201, 201, 201);
}
.login__form input:focus{
    border:1px solid #d6d6d6;
    transition: all 0.2s;
}
.login__divider{
    display: flex;
    margin-bottom: 20px;
}
.login__divider hr{
    width: 100%;
    height: 0;
    opacity: 0.3;
    margin: 5px;
}
.login__divider span{
      font-weight: 500;
      color: #5c5e62;
}
